<template>
  <v-card outlined :key="renderCount">
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      <span>Devices</span><span class="caption pl-3" v-if="networkName">Network: {{ networkName }} </span>
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <rf-device-grid v-if="params.uuid" :params="params" :getDataCb="getDataCb"> </rf-device-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import _get from 'lodash/get';
import IconButton from '../../Common/IconButton.vue';
import RfDeviceForm from './rfDeviceForm.vue';
import RfDeviceGrid from './rfDeviceGrid.vue';
import routeConstant from '../../../constants/routes';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';

const baseHref = routeConstant.rfNetworkDevicePoint;
const initialBreadCrumbItem = {
  text: 'Networks',
  disabled: false,
  link: baseHref,
};

export default {
  name: 'rf-devices',
  components: {
    IconButton,
    RfDeviceForm,
    RfDeviceGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  data() {
    return {
      params: {
        // fetch api flags
        uuid: null,
        with_devices: true,
      },
      renderCount: 0,
      networkName: '',
      gridData: {},
      breadcrumbItems: [initialBreadCrumbItem],
    };
  },
  watch: {
    '$route.params.networkId': function (val, oldVal) {
      if (val != oldVal) {
        this.params = { ...this.params, uuid: this.$route.params.networkId };
        this.renderCount++;
      }
    },
  },
  mounted() {
    this.params = { ...this.params, uuid: this.$route.params.networkId };
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.updateBreadCrumbs();
    },
    updateBreadCrumbs() {
      this.breadcrumbItems = [];
      this.breadcrumbItems.push(initialBreadCrumbItem);
      const networkUUID = _get(this.gridData, 'extraData.uuid', '');
      const networkName = _get(this.gridData, 'extraData.name', '');
      if (networkUUID) {
        this.networkName = networkName;
        this.breadcrumbItems.push({ text: networkUUID, align: 'center', disabled: true });
        this.breadcrumbItems.push({ text: 'Devices', align: 'center', disabled: true });
      }
    },
  },
};
</script>
