<template>
  <div>
    <generic-grid
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
      :getDataCb="getDataCb"
    >
      <template v-slot:routeLinkName="{ item }">
        <text-button :text="item.name" :onClick="() => redirectTo(dialogNames.viewPoints, item)"></text-button>
      </template>
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <rf-device-form
          v-if="isOpen"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></rf-device-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <rf-device-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></rf-device-form>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import RfDeviceForm from './rfDeviceForm.vue';
import IconButton from '../../Common/IconButton.vue';
import TextButton from '../../Common/TextButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import { rfDeviceService } from '../../../services/rfDeviceService';
import ROUTES from '../../../constants/routes';

const dialogNames = {
  viewPoints: 'viewPoints',
};

const redirectMap = {
  [dialogNames.viewPoints]: (networkId, deviceId) =>
    ROUTES.rfPoints.replace(':networkId', networkId).replace(':deviceId', deviceId),
};

export default {
  name: 'rf-device-grid',
  components: { GenericGrid, RfDeviceForm, IconButton, TextButton },
  props: ['params', 'getDataCb'],
  data() {
    return {
      dialogNames: dialogNames,
      service: rfDeviceService,
      toolbarTitle: 'Devices',
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'routeLinkName',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  methods: {
    redirectTo(viewName, item) {
      const networkId = this.$route.params.networkId;
      const deviceId = item.uuid;
      if (!networkId || !deviceId) {
        return;
      }

      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(networkId, deviceId) });
      }
    },
  },
};
</script>
